

import { BidiModule } from '@angular/cdk/bidi';
import { PlatformModule } from '@angular/cdk/platform';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { TDSButtonModule } from 'tds-ui/button';
import { TDSI18nModule } from 'tds-ui/i18n';
import { TDSProgressModule } from 'tds-ui/progress';
import { TDSSpinnerModule } from 'tds-ui/progress-spinner';
import { TDSToolTipModule } from 'tds-ui/tooltip';


import { TDSUploadBtnComponent } from './upload-btn.component';
import { TDSUploadListComponent } from './upload-list.component';
import { TDSUploadComponent } from './upload.component';

@NgModule({
  imports: [
    BidiModule,
    CommonModule,
    FormsModule,
    PlatformModule,
    TDSToolTipModule,
    TDSProgressModule,
    TDSI18nModule,
    TDSButtonModule,
    TDSSpinnerModule
  ],
  declarations: [TDSUploadComponent, TDSUploadBtnComponent, TDSUploadListComponent],
  exports: [TDSUploadComponent]
})
export class TDSUploadModule {}
