

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { Observable, of, Subject, Subscription } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { TDSI18nService, TDSUploadI18nInterface } from 'tds-ui/i18n';
import { InputBoolean, InputNumber, TDSSafeAny, toBoolean } from 'tds-ui/shared/utility';


import {
  TDSIconRenderTemplate,
  TDSShowUploadList,
  TDSUploadChangeParam,
  TDSUploadFile,
  TDSUploadListType,
  TDSUploadTransformFileType,
  TDSUploadType,
  TDSUploadXHRArgs,
  UploadFilter,
  ZipButtonOptions
} from './interface';
import { TDSUploadBtnComponent } from './upload-btn.component';
import { TDSUploadListComponent } from './upload-list.component';

@Component({
  selector: 'tds-upload',
  exportAs: 'tdsUpload',
  templateUrl: './upload.component.html',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    'class':"inline",
    '[class.tds-upload-picture-card-wrapper]': 'listType === "picture-card"'
  }
})
export class TDSUploadComponent implements OnInit, OnChanges, OnDestroy {
  static ngAcceptInputType_limit: NumberInput;
  static ngAcceptInputType_size: NumberInput;
  static ngAcceptInputType_directory: BooleanInput;
  static ngAcceptInputType_isOpenFileDialogOnClick: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_multiple: BooleanInput;
  static ngAcceptInputType_showUploadList: BooleanInput | TDSShowUploadList;
  static ngAcceptInputType_showButton: BooleanInput;
  static ngAcceptInputType_withCredentials: BooleanInput;

  private destroy$ = new Subject<void>();
  @ViewChild('uploadComp', { static: false }) uploadComp!: TDSUploadBtnComponent;
  @ViewChild('listComp', { static: false }) listComp!: TDSUploadListComponent;

  locale!: TDSUploadI18nInterface;
  dir: Direction = 'ltr';

  // #region fields

  @Input() type: TDSUploadType = 'select';
  @Input() @InputNumber() limit = 0;
  @Input() @InputNumber() size = 0;

  @Input() fileType?: string;
  @Input() accept?: string | string[];
  @Input() action?: string | ((file: TDSUploadFile) => string | Observable<string>);
  @Input() @InputBoolean() directory = false;
  @Input() @InputBoolean() isOpenFileDialogOnClick = true;
  @Input() beforeUpload?: (file: TDSUploadFile, fileList: TDSUploadFile[]) => boolean | Observable<boolean>;
  @Input() customRequest?: (item: TDSUploadXHRArgs) => Subscription;
  @Input() data?: {} | ((file: TDSUploadFile) => {} | Observable<{}>);
  @Input() filter: UploadFilter[] = [];
  @Input() fileList: TDSUploadFile[] = [];
  @Input() @InputBoolean() disabled = false;
  @Input() headers?: {} | ((file: TDSUploadFile) => {} | Observable<{}>);
  @Input() listType: TDSUploadListType = 'text';
  @Input() @InputBoolean() multiple = false;
  @Input() name = 'file';

  private _showUploadList: boolean | TDSShowUploadList = true;

  @Input()
  set showUploadList(value: boolean | TDSShowUploadList) {
    this._showUploadList = typeof value === 'boolean' ? toBoolean(value) : value;
  }

  get showUploadList(): boolean | TDSShowUploadList {
    return this._showUploadList;
  }

  @Input() @InputBoolean() showButton = true;
  @Input() @InputBoolean() withCredentials = false;

  @Input() remove?: (file: TDSUploadFile) => boolean | Observable<boolean>;
  @Input() preview?: (file: TDSUploadFile) => void;
  @Input() previewFile?: (file: TDSUploadFile) => Observable<string>;
  @Input() previewIsImage?: (file: TDSUploadFile) => boolean;
  @Input() transformFile?: (file: TDSUploadFile) => TDSUploadTransformFileType;
  @Input() download?: (file: TDSUploadFile) => void;
  @Input() iconRender: TDSIconRenderTemplate | null = null;
  @Input() fileListRender: TemplateRef<void> | null = null;

  @Output() readonly uploadChange: EventEmitter<TDSUploadChangeParam> = new EventEmitter<TDSUploadChangeParam>();
  @Output() readonly fileListChange: EventEmitter<TDSUploadFile[]> = new EventEmitter<TDSUploadFile[]>();

  _btnOptions?: ZipButtonOptions;

  private zipOptions(): this {
    if (typeof this.showUploadList === 'boolean' && this.showUploadList) {
      this.showUploadList = {
        showPreviewIcon: true,
        showRemoveIcon: true,
        showDownloadIcon: true
      };
    }
    // filters
    const filters: UploadFilter[] = this.filter.slice();
    if (this.multiple && this.limit > 0 && filters.findIndex(w => w.name === 'limit') === -1) {
      filters.push({
        name: 'limit',
        fn: (fileList: TDSUploadFile[]) => fileList.slice(-this.limit)
      });
    }
    if (this.size > 0 && filters.findIndex(w => w.name === 'size') === -1) {
      filters.push({
        name: 'size',
        fn: (fileList: TDSUploadFile[]) => fileList.filter(w => w.size! / 1024 <= this.size)
      });
    }
    if (this.fileType && this.fileType.length > 0 && filters.findIndex(w => w.name === 'type') === -1) {
      const types = this.fileType.split(',');
      filters.push({
        name: 'type',
        fn: (fileList: TDSUploadFile[]) => fileList.filter(w => ~types.indexOf(w.type!))
      });
    }
    this._btnOptions = {
      disabled: this.disabled,
      accept: this.accept,
      action: this.action,
      directory: this.directory,
      openFileDialogOnClick: this.isOpenFileDialogOnClick,
      onBeforeUpload: this.beforeUpload,
      customRequest: this.customRequest,
      data: this.data,
      headers: this.headers,
      name: this.name,
      multiple: this.multiple,
      withCredentials: this.withCredentials,
      filters,
      transformFile: this.transformFile,
      onStart: this.onStart,
      onProgress: this.onProgress,
      onSuccess: this.onSuccess,
      onError: this.onError
    };
    return this;
  }

  // #endregion

  constructor(
    private cdr: ChangeDetectorRef,
    private i18n: TDSI18nService,
    @Optional() private directionality: Directionality
  ) {}

  // #region upload

  private fileToObject(file: TDSUploadFile): TDSUploadFile {
    return {
      lastModified: file.lastModified,
      lastModifiedDate: file.lastModifiedDate,
      name: file.filename || file.name,
      size: file.size,
      type: file.type,
      uid: file.uid,
      response: file.response,
      error: file.error,
      percent: 0,
      originFileObj: file as TDSSafeAny
    };
  }

  private getFileItem(file: TDSUploadFile, fileList: TDSUploadFile[]): TDSUploadFile {
    return fileList.filter(item => item.uid === file.uid)[0];
  }

  private removeFileItem(file: TDSUploadFile, fileList: TDSUploadFile[]): TDSUploadFile[] {
    return fileList.filter(item => item.uid !== file.uid);
  }

  private onStart = (file: TDSUploadFile): void => {
    if (!this.fileList) {
      this.fileList = [];
    }
    const targetItem = this.fileToObject(file);
    targetItem.status = 'uploading';
    this.fileList = this.fileList.concat(targetItem);
    this.fileListChange.emit(this.fileList);
    this.uploadChange.emit({ file: targetItem, fileList: this.fileList, type: 'start' });
    this.detectChangesList();
  };

  private onProgress = (e: { percent: number }, file: TDSUploadFile): void => {
    const fileList = this.fileList;
    const targetItem = this.getFileItem(file, fileList);
    targetItem.percent = e.percent;
    this.uploadChange.emit({
      event: e,
      file: { ...targetItem },
      fileList: this.fileList,
      type: 'progress'
    });
    this.detectChangesList();
  };

  private onSuccess = (res: {}, file: TDSUploadFile): void => {
    const fileList = this.fileList;
    const targetItem = this.getFileItem(file, fileList);
    targetItem.status = 'done';
    targetItem.response = res;
    
    this.uploadChange.emit({
      file: { ...targetItem },
      fileList,
      type: 'success'
    });
    this.detectChangesList();
  };

  private onError = (err: {}, file: TDSUploadFile): void => {
    const fileList = this.fileList;
    const targetItem = this.getFileItem(file, fileList);
    targetItem.error = err;
    targetItem.status = 'error';
    this.uploadChange.emit({
      file: { ...targetItem },
      fileList,
      type: 'error'
    });
    this.detectChangesList();
  };

  // #endregion

  // #region drag

  private dragState?: string;

  // skip safari bug
  fileDrop(e: DragEvent): void {
    if (e.type === this.dragState) {
      return;
    }
    this.dragState = e.type;
    this.setClassMap();
  }

  // #endregion

  // #region list

  private detectChangesList(): void {
    this.cdr.detectChanges();
    this.listComp?.detectChanges();
  }

  onRemove = (file: TDSUploadFile): void => {
    this.uploadComp.abort(file);
    file.status = 'removed';
    const fnRes =
      typeof this.remove === 'function' ? this.remove(file) : this.remove == null ? true : this.remove;
    (fnRes instanceof Observable ? fnRes : of(fnRes)).pipe(filter((res: boolean) => res)).subscribe(() => {
      this.fileList = this.removeFileItem(file, this.fileList);
      this.uploadChange.emit({
        file,
        fileList: this.fileList,
        type: 'removed'
      });
      this.fileListChange.emit(this.fileList);
      this.cdr.detectChanges();
    });
  };

  // #endregion

  // #region styles

  private prefixCls = 'tds-upload';
  classList: string[] = [];

  private setClassMap(): void {
    let subCls: string[] = [];
    if (this.type === 'drag') {
      if (this.fileList.some(file => file.status === 'uploading')) {
        subCls.push(`${this.prefixCls}-drag-uploading`);
      }
      if (this.dragState === 'dragover') {
        subCls.push(`${this.prefixCls}-drag-hover`);
      }
    } else {
      subCls = [`${this.prefixCls}-select-${this.listType}`];
      if(this.listType === "picture-card")
      {
        subCls.push('w-28 h-28 inline-flex border border-dashed border-neutral-2-200 dark:border-d-neutral-2-200 rounded  mt-2.5 items-center justify-center ');
      }
    }

    this.classList = [
      this.prefixCls,
      `${this.prefixCls}-${this.type} `,
      ...subCls,
      (this.disabled && `${this.prefixCls}-disabled`) || '',
      (this.dir === 'rtl' && `${this.prefixCls}-rtl`) || ''
    ].filter(item => !!item);

    this.cdr.detectChanges();
  }

  // #endregion

  ngOnInit(): void {
    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.setClassMap();
      this.cdr.detectChanges();
    });

    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Upload');
      this.detectChangesList();
    });
  }

  ngOnChanges(): void {
    this.zipOptions().setClassMap();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
